.video-block {
  margin: 0;
  padding: 0;
}

.video-block__responsive {
  background-color: $color--black;
  height: 0;
  line-height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;

  iframe {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }
}

.video-block__caption {
  @include caption-long;
}
